import React from 'react'
import styled from 'styled-components'
import { Card } from '@robustswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import TwitterCard from './TwitterCard'

const StyledLimitStatsCard = styled(Card)`
  width: 100%;
  max-width: 320px !important;
  border-radius: 16px 16px 0px 0px;
  background: #1E215C;
  padding: 16px 16px 0px 16px;
`

const LabelTitle = styled.div`
  margin-bottom:8px;
  color: #A0B9FB;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
`

const LabelSubTitle = styled.div`
  margin-bottom:8px;
  color: #FCFCFC;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
`

const LimitStatsCard = () => {
  const TranslateString = useI18n()

  return (
    <StyledLimitStatsCard>
      <LabelTitle>{TranslateString(10039, 'Follow us on twitter')}</LabelTitle>{/* <CardValue fontSize="20px" value={getBalanceNumber(totalLiquidityUSD, 0)} decimals={0} prefix="$" /> */}
      <LabelSubTitle>@robustprotocol</LabelSubTitle>
      <TwitterCard />
    </StyledLimitStatsCard>
  )
}

export default LimitStatsCard